/*--------------------------------------------------------------
# General
--------------------------------------------------------------*/
* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  white-space: no-wrap;
  text-decoration: none;
  /* border: 1px solid white; */
  
}
*:focus{
  outline: none;
}

html{
    overscroll-behavior: none;
    scroll-behavior: smooth;
    
}
body {
  background-color: black;
  width: 100%;  
  overflow-x: hidden;
  transition: 1s ease;
}

.loading{
  height: 100vh;
  overflow: hidden;
  /* display: none; */
}
img{
  pointer-events: none;
}

/*--------------------------------------------------------------
# PreLoader
--------------------------------------------------------------*/
#preloader{
  position:fixed;
  top:0;
  left:0;
  width: 100%;
  height: 100vh;
  background-color: black;
  z-index: 9999;
  transition: 0.5s ease;
  margin: 0;
}
.dot-flashing {
  position: absolute;
  bottom: 2%;
  right: 2%;
  width: 1vw;
  max-width: 8px;
  height: 1vw;
  max-height: 8px;
  border-radius: 50%;
  background-color: #fef879;
  color: #fef879;
  animation: dot-flashing 1s infinite linear alternate;
  animation-delay: 0.5s;
}
.dot-flashing::before, .dot-flashing::after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
}
.dot-flashing::before {
  left: -1.2vw;
  width: 1vw;
  max-width: 8px;
  height: 1vw;
  max-height: 8px;
  border-radius: 50%;
  background-color: #fef879;
  color: #fef879;
  animation: dot-flashing 1s infinite alternate;
  animation-delay: 0s;
}
.dot-flashing::after {
  left: 1.2vw;
  width: 1vw;
  max-width: 8px;
  height: 1vw;
  max-height: 8px;
  border-radius: 50%;
  background-color: #fef879;
  color: #fef879;
  animation: dot-flashing 1s infinite alternate;
  animation-delay: 1s;
}

@keyframes dot-flashing {
  0% {
    background-color: rgb(254, 248, 121);
  }
  50%, 100% {
    background-color: rgba(254, 248, 121,0.2);
  }
}
.preLogo{
  position: absolute;
  left:50%;
  top:50%;
  transform: translate(-50%,-50%);
  height: auto;
  width: 20%;
  -webkit-filter: drop-shadow(5px 5px 5px #fef879);
  filter: drop-shadow(1px 1px 5px #fef879);
  transition: 0.5s all;
  animation: blinking 4s linear infinite;
}
.box1{
  position:absolute;
  width: auto;
  height: 50vh;
  top: 45%;
  left: 28%;
  transform: translate(-50%,-50%);
  animation: floating11 3s 0.2s  ease-in-out infinite;
}
.box2{
  position:absolute;
  width: auto;
  height: 50vh;
  top: 50%;
  left: 23.8%;
  transform: translate(-50%,-50%);
  animation: floating11 3s 0.3s ease-in-out infinite;
}
.box3{
  position:absolute;
  width: auto;
  height: 50vh;
  top: 50%;
  right: 6%;
  transform: translate(-50%,-50%);
  animation: floating11 3s 0.2s ease-in-out infinite;
}

@keyframes floating11 {
  0% { transform: translate(-50%, -50%); }
  50%  { transform: translate(-50%,-48%); }
  100%   { transform: translate(-50%, -50%); }    
}

.circular-progress-div {
  position: absolute;
  bottom: 0%;
  left: 50%;
  transform: translate(-50%,0%);
  z-index: 9999;
  width: 100%;
  height: 30vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
}
.circular-progress{
  position: absolute;
  top: 32%;
  left: 50%;
  transform: translate(-50%,0%);
  z-index: 9999;
  width: 7vh;
  height: 7vh;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ab9f75;
  padding: 0;
  margin: 0;
}
.inner-circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: calc(7vh - 5px);
  height: calc(7vh - 5px);
  border-radius: 50%;
  background-color: black;
}
.loadText{
  padding: 0;
  margin: 0;
  opacity: 0;
  font-family: 'Teko',sans-serif;
  text-align: center;
  position: absolute;
  bottom: 18%;
  left: 50%;
  transform: translate(-50%,0%);
  width: 100%;
  height: fit-content;
  font-size: max(2vw,20px);
  color: #adacac;
  animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

@keyframes fade-in {
  100% {
    opacity: 1;
  }
}

.percentage {
  position: absolute;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  font-size: 3vh;
  line-height: 10vh;
  font-family: 'Teko',sans-serif;
  color: rgb(0, 0, 0, 0.8);
}

