.slider{
  position: relative;
  z-index: 800;
  padding-top: 5vh;
  padding-bottom: 5vh;
  cursor: pointer;
 
}
.sliderOverlay{
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 9990;
  pointer-events: none;
  background: rgb(0,0,0);
  background: linear-gradient(90deg, rgba(0,0,0,0.691841702501313) 2%, rgba(0,0,0,0.33049716468618695) 8%, rgba(0,0,0,0) 29%, rgba(0,0,0,0) 71%, rgba(0,0,0,0.3360994055825455) 92%, rgba(0,0,0,0.6946428229494923) 98%);
}
.slider .card{
  overflow: hidden;
  height: 55vh;
  min-height: 300px;
  width: 22vw;
  border: 1px solid rgba(255, 255, 255, 0.2);
	border-top: 1px solid rgba(255,255,255,0.2);
  border-bottom: 2px solid #bda000;
	border-left: 1px solid rgba(255,255,255,0.2);
	background: rgba(255,255,255,0.1);
}
.slider .card img{
  height: 50%;
}
.slider .card-body{
  height: 50%;
  margin-top: 10%;
  color: white;
  font-size: 1.6vw;
  text-align: justify;
}
.slider .card-title{
  color: black;
  position: absolute;
  height: 5vh;
  min-height: 20px;
  width: 110%;
  left: -5%;
  top:50%;
  transform: translate(0%,-50%);
  rotate: -2deg;
  text-align: center;
  font-size: 2vw;
  line-height: max(5vh,20px);
  font-family: "A4 Speed Bold", sans-serif;
  background: #bda000;
  background: linear-gradient(0deg, rgba(189,160,0,1) 0%, rgba(255,219,73,1) 50%, rgba(227,145,15,1) 100%);    
}
.slider .sii{
  font-size: 1.5vw !important;
}
.slide__item { 
  display: flex;
  justify-content: center;
  align-items: center;
}
