.marquee_Background{
  margin: 0;
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
  background-color: black;
}
.translatingDiv{
  position: absolute;
  top: min(-2vw,-150px);
  left: min(-30%,-300px);
}
.marquee {
  position: relative;
  z-index: 100;
  --duration: 20s;
  --gap: 1.5vw;
  font-size: 1.5vw;
  display: flex;
  user-select: none;
  gap: var(--gap);
  rotate: -50deg;
  margin-bottom: 4vw; 
}
.marquee__group {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: var(--gap);
  min-width: 100%;
  animation: scroll var(--duration) linear infinite;
}
.marquee__group .card {
  color: rgb(255,255,255,0.9);
  width: 20vw;
  height: 15vw;
  margin-top: 3vw;
  margin-bottom: 3vw;
	border-radius: 1.5vw;
  border: 1px solid rgba(255, 255, 255, 0.2);
	border-top: 1px solid rgba(255,255,255,0.2);
	border-left: 1px solid rgba(255,255,255,0.2);
	background: rgba(255,255,255,0.1);
  overflow: hidden;
  transition: 0.3s ease;
}
.marquee__group .card:hover{
  cursor: pointer;
  background: rgb(255,139,0) !important;
  background: linear-gradient(95deg, rgba(255,139,0,1) 0%, rgba(255,134,0,0.6937149859943977) 19%, rgba(255,204,96,0) 100%) !important;
}
.marquee--reverse .marquee__group {
  animation-direction: reverse;
  animation-delay: calc(var(--duration) / -2);
}
@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  25%{
    transform: translateX(calc(-20%));
  }
  50%{
    transform: translateX(calc(-30%));
  }
  75%{
    transform: translateX(calc(-20%));
  }
  100% {
    transform: translateX(0);
  }
}

