/*--------------------------------------------------------------
# DressChangeDiv
--------------------------------------------------------------*/
.dressChangeDiv{
  /* border: 1px solid white; */
  margin-top: 10vh;
  padding: 0;
  width: 100%;
  height: 100vh;
  min-height: 500px;
  position: relative;
  overflow: hidden;
  background-color: black;
  }

.dressChangeDiv video{
  position: absolute;
  z-index: 1;
  top: 0;
  left: 50%;
  transform: translate(-50%,0%);
  height: 100vh;
  min-height: 500px;
  width: auto;
  overflow: hidden;
  box-shadow: inset 1px 10px 10px black;
}

.dressChangeDiv .shotRow{
  position: absolute;
  z-index: 9990;
  width: fit-content;
  bottom: 5%;
  left: 49.2%;
  transform: translate(-50%,0%);
  background-color: transparent;
  cursor: pointer;
}

.shotButton{
  cursor: pointer;
  font-family: 'Teko', sans-serif;
  padding-top: 0.6vw;
  line-height: 2vw;
  font-size: 2vw;
  font-weight: 550;
  text-align: center;
  width: fit-content;
  padding-left: 1vw;
  padding-right: 1vw;
  background: rgb(189,160,0);
  background: linear-gradient(180deg, rgba(189,160,0,1) 0%, rgba(255,219,73,1) 50%, rgba(227,145,15,1) 100%);
  outline: none;
  border: none;
  color: black;
  overflow: hidden;
  position: relative;
  transition: 1s all linear;
}

.shotButton:hover {
	cursor: pointer;
}
.shotButton .glare {
  animation: moveright 2.5s linear infinite;
	
	-webkit-transition: all 0.6s linear;
	      transition: all 0.6s linear;
}
@keyframes moveright {
  40%{
    right: 0%;
  }
  100%{
    right: -45%;
  }
  
}
.glare:active {
	opacity: 0.95;
}
.glare {
	position: absolute;
	width: 25px;
	height: 200%;
	-webkit-transform: rotate(25deg);
	-ms-transform: rotate(25deg);
	transform: rotate(25deg);
	background-color: rgba(255, 255, 255, 1);
	top: -50%;
	right: 120%;
  opacity: 0.3;
}
.glare:after {
	content: '';
	display: none;
	position: absolute;
	width: 10px;
	height: 100%;
	background: inherit;
	right: -15px;
	top: 0;
}
.shotButton:focus{
  outline: none;
  border: none;
}
#shotVideo{
  z-index: 1;
  display: none;
}
#idleVideo{
  display: block;
  z-index: 10;
}
