/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/

.footer{
  height: fit-content;
  min-height: 300px;
  position: relative;
  z-index: 100;
  padding-top: 50px;
  background-color: #000;
  color: white;
  overflow: hidden;
  text-shadow: 1px 4px 1px black;
}
.card__grid-effect {
  position: absolute;
  /* border: 1px solid red; */
  top: 0%;
  height: 40vh;
  z-index: 0;
  inset: 0;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
}
.card__grid-effect-tile {
  position: relative;
  /* border: 1px solid white; */
}
.card__grid-effect-tile:before {
  content: "";
  color: rgb(185, 179, 179);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 2px;
  width: 2px;
  border-radius: 50%;
  background: rgb(185, 179, 179);
  transition: 0.2s ease all;
}
.card__grid-effect-tile:hover:before {
  cursor: pointer;
  height: 10px;
  width: 10px;
  transition: 70ms linear all;
}
.footerRow1{
  overflow: hidden;
}
.footerBannerTitle{
  font-weight: 900;
  font-size: 2.5vw;
  line-height: 0.8;
}
